html, body {
  min-height: 100vh;
  background-color: #414147;
  color: #eee;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  font-feature-settings: "palt";
  -ms-overflow-style: none;
  scrollbar-width: none;
}

*::-webkit-scrollbar {
  display: none;
}

a {
  color: inherit;
  text-decoration: none;
}

input {
  -webkit-appearance: none;
}

.App {
  
}

header {
  display: flex;
  flex-wrap: nowrap;
  overflow: scroll;
  position: sticky;
  border-bottom: 1px solid #aaa;
}

.Gnav {
  display: flex;
  padding: 10px;
}

.Gnav__item {
  padding: 5px;
  font-weight: bold;
}

.Gnav__item:nth-of-type(n + 2) {
  margin: 0 0 0 15px;
}

main {
  margin: auto;
  padding: 20px;
  max-width: 500px;
}

/* Modal */
.Overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  align-self: inherit;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
}

.Overlay .Content {
  width:80%;
  padding: 1em;
  background-color: #414147;
}

.ModalTitle {
  margin: 0 auto 20px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}

.ModalButtons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ModalButtons button {
  padding: 0.5em 0;
  width: 45%;
  font-size: 13px;
}

.CancelButton {
  border: 1px solid #eee;
  background-color: transparent;
  color: #eee;
}

.DeleteButton {
  border: 1px solid rgb(240, 0, 0);
  background-color: rgb(240, 0, 0);
  color: #eee;
}

/* CounterApp */
.CounterApp {
  text-align: center;
}

.CountItem {
  padding: 20px;
  font-size: 30px;
}

.CountButtons {

}

.CountButtons button {
  padding: 10px 0;
  width: 50%;
  font-size: 20px;
}

/* TodoApp */
.TodoApp {
  padding: 20px 0 0;
}

.TodoForm {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  font-size: 18px;
}

.TodoInput {
  border-radius: 0;
  flex: 1;
  margin: 0 0 0 10px;
  border: 1px solid #aaa;
  padding: 0.2em 0.5em;
  width: 100%;
  background-color: transparent;
  font-size: 16px;
  outline: none;
  color: #eee;
}

.TodoList {
  padding:10px 10px 0;
}

.TodoItem {
  display: flex;
  align-items: center;
  margin: 0 0 10px;
  letter-spacing: 0.1em;
}

.TodoItem:last-child {
  margin: 0;
}

.TodoItem input {
  margin: 0 1em 0 0;
}

.TodoItem span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 0 auto;
  border-radius: 50%;
  width: 22px;
  height: 22px;
}

/* TodoApp */
.SearchMovieApp {
  padding: 20px 0 0;
}

form {
  display: flex;
  flex-wrap: nowrap;
  margin: 0 auto 0.5em;
}

.SearchField {
  border-radius: 0;
  outline: none;
  border: solid #aaa;
  border-width: 1px 0 1px 1px;
  padding: 0.2em 0.5em;
  width: 100%;
  background-color: transparent;
  font-size: 16px;
  color: #eee;
}

.SubmitButton {
  border-radius: 0;
  border: 1px solid #eee;
  padding: 0.2em 0.5em;
  background-color: #eee;
  font-size: 16px;
  font-weight: bold;
  color: #414147;
}

.Movies {
  display: flex;
  flex-wrap: nowrap;
  padding: 50px 0 0;
  overflow-x: scroll;
}

.Movie {
  margin: 0 30px 0 0;
  width: 300px;
}

.Movie h3 {
  height: 2.5em;
  font-size: 20px;
}

